import React from 'react';
import styled, { css } from 'react-emotion';
import { hideText, position } from 'polished';
import { getSiteData } from 'react-static';
import { Section, Container, Text, List, ListItem } from 'css/basicElements';
import TextLink from 'components/TextLink';


const TopText = props => {
  return (
    <span>
    The complexity of digital identity is as unique and varied as
    all of humanity and grows ever more complex when organizations,
    animals, and objects are included.  A lack of richly annotated
    reference cases hinders objective and comparative analysis of
    the capabilities and liabilities of managed and decentralized
    identity systems.  Alice and Bob simply do not capture the full
    range of identity configurations, requirements, and relationships
    and a larger, expanded, reference set is required.<br />
    Furthermore, larger database of reference cases is required when
    assessing legal frameworks such as the <TextLink to="https://www.evernym.com/wp-content/uploads/2017/07/SovrinProvisionalTrustFramework2017-03-22.pdf">
    Sovrin Trust Framework</TextLink>, domain specific
    trust frameworks, and the explicit and implicit trust frameworks
    deployed by other systems, such as those effectively relying
    upon the KYC procedures of cryptocurrency exchanges.
    </span>
  )
}

const Component = props => {
    const { siteTitle } = getSiteData;
    const setWidth = css`
        width: ${props.width}px;
    `;
    return (
      <Container className="container">
        <h3 className="h4">Reference Test Cases</h3>
      </Container>
);
};

export default {
    anchor : '#operations',
    headline: 'Use Cases',
    body: <TopText />,
    component:<Component />
};



