import React from 'react';
import styled, { css } from 'react-emotion';
import { hideText, position } from 'polished';
import { getSiteData } from 'react-static';
import { Section, Container, Text, List, ListItem } from 'css/basicElements';


const TopText = props => {
  return (
    <span>
      Managed identity systems, such as the IAM environments operated by governments and
      employers, are a vital and integral part of modern life.  Regulatory
      requirements are often at odds with the principles
      of self-sovereignty, resulting in pragmatically mandatory surrender
      of sovereignty.  While you may choose to participate in social
      networks or consumer loyalty programs, you may not choose to participate
      in governmental systems.  For pragmatic reasons it may not be possible
      to opt out of employer driven systems, resulting in loss of sovereignty
      in exchange for a paycheck.  Cryptographic and best
      practice solutions to these problems are well established, but bridging
      the gap is a more difficult battle.  Can we visualize the points at
      which governments, regulatory agencies, and employment practices
      introduce risk and threaten sovereignty.
    </span>
  )
}

const Component = props => {
    const { siteTitle } = getSiteData;
    const setWidth = css`
        width: ${props.width}px;
    `;
    return (
      <Container className="container">
        <h3 className="h4">Bridging Managed Identity with Self-Sovereignty</h3>
      </Container>
);
};

export default {
    anchor : '#bridge',
    headline: 'Managed/Sovereign Bridge',
    body: <TopText />,
    component:<Component />
};



