import React from 'react';
import styled from 'react-emotion';
import { palette } from 'css/variables';
import { gradientBg, positions } from 'css/utilities';

const BgContour = styled('span')`
    ${positions.absCenter()};
    z-index: 0;
    display: block;
    background-color: black;
    background-image: radial-gradient(circle at center 40%, rgba(22,24,23,1) 50%, rgba(0,0,0,0) 150%);
`;

const BgGradient = styled('span')`
    ${positions.absCenter()};
    z-index: 0;
    display: block;
    background-color: ${palette.greenDarkestAlt};
    ${gradientBg};
    background-blend-mode: darken;
`;

export default () => (
    <React.Fragment>
        <BgContour className="bg-contour" />
        <BgGradient className="bg-gradient" />
    </React.Fragment>    
)




