import React from 'react';
import styled, { css } from 'react-emotion';
import { hideText, position } from 'polished';
import { getSiteData } from 'react-static';
import { Section, Container, Text, List, ListItem } from 'css/basicElements';
import TextLink from 'components/TextLink';

const TopText = props => {
  return (
    <span>
    The risks of centrally managed systems or systems controlled by a
    single organization are well understood.  Decentralized systems address
    those risks, but are subject to the same forces of consolidation,
    especially when it comes to operations.  How do we measure the operational
    diversity of decentralized systems and equitably compare them with the
    risks from centralized systems?  How can we objectively assess and
    visualize Diffuse Trust and System Diversity as described in the
    <TextLink to="https://www.evernym.com/wp-content/uploads/2017/07/SovrinProvisionalTrustFramework2017-03-22.pdf">
    Sovrin Trust Framework</TextLink>, and how can we assess these
    concepts in other identity systems.
    What dangers lurk in our reliance
    upon only a small number of smart phone vendors and carriers?  What
    transaction volumes are claimed by blockchain solutions and how do
    those claims match objective measurement and monitoring?
    </span>
  )
}

const Component = props => {
    const { siteTitle } = getSiteData;
    const setWidth = css`
        width: ${props.width}px;
    `;
    return (
      <Container className="container">
        <h3 className="h4">Operational Assessment</h3>
        <p>
        The Sovrin Foundation
        </p>
      </Container>
);
};

export default {
    anchor : '#operations',
    headline: 'Operations Analysis',
    body: <TopText />,
    component:<Component />
};



