import styled from 'react-emotion';
import { rem, clearFix, transparentize } from 'polished';
import propStyles from 'prop-styles';
import { text, list } from 'css/utilities';
import { palette } from 'css/variables';

export const Container = styled('div')`
    max-width: 1170px;
    margin: auto;
    padding: ${rem('36px', '16px')};
`;

export const Section = styled('section')`
    position: relative;
    min-height: 100vh;
    ${clearFix};
`;

export const Text = styled('p')`
    ${propStyles({
        textSm: [text.small],
        textMd: [text.medium],
        textMdLg: [text.mediumLg],
        textLg: [text.large],
        textXlg: [text.xLarge],
        textHuge: [text.huge],

        center: [text.center],
        left: [text.left],
        right: [text.right],
        justify: [text.justify],

        grayLight: [text.grayLight],
        greenNeon: [text.greenNeon],
        green: [text.green],
        greenDarker: [text.greenDarker],
        greenDarkest: [text.greenDarkest],
    })};
    position: relative;
    z-index: 2;
`;

export const List = styled('ul')`
    ${propStyles({
        noBullet: [list.none],
        square: [list.square],
        bullet: [list.bullet],
    })};

    margin: 0;
    padding: 0;
`;

export const ListItem = styled('li')`
    //font-size: ${rem('18px', '16px')};
    margin: 9px 0 0 0;
    -webkit-column-break-inside: avoid;
          page-break-inside: avoid;
               break-inside: avoid;
    &:first-child {
        margin-top: 0;
    }
    h2, h3 {
        margin-top: 0;
    }
`;

export const Code = styled('span')`
  margin-left: 0.25em;
  margin-right: 0.25em;
  font-family: monospace
`;
export const LinkStyle = styled('span')`
  margin-left: 0.25em;
  margin-right: 0.25em;
  color: ${palette.grayLighter};
`;

export const HeaderMessage = styled('div')`
    margin: 1.414em 0 0.5em;
    font-weight: 600;
    line-height: 1;
    font-size: 1.618em;
    margin-top: 0;
`;
export const TrailerMessage = styled('div')`
    margin: 1.414em 0 0.5em;
    font-weight: 400;
    line-height: 1;
    font-size: 1.3em;
    text-align:center;
`;
export const ListItemLink = styled('span')`
    margin: 1.414em 0 0.5em;
    font-weight: 300;
    line-height: 1;
    font-size: 1.3em;
    margin-top: 0;
    color: ${palette.grayLightest};
`;
export const ListItemBody = styled('div')`
    margin: 1.414em 0 0.5em;
    font-weight: 300;
    line-height: 1;
    font-size: 1em;
    margin-top: 0.5em;
`;



