import React from 'react';
import styled, { css } from 'react-emotion';
import { hideText, position } from 'polished';
import { getSiteData } from 'react-static';
import { Section, Code, Container, Text, List, ListItem } from 'css/basicElements';
import TextLink from 'components/TextLink';


const TopText = props => {
  return (
    <span>
      Deploying testbeds for identity system development is complicated
      and is a barrier to the global adoption and deep integration
      required to reclaim the sovereignty of identity.  Tasks such as a
      functioning Hyperledger environment, spinning up a local,
      non testchain Bitcoin or uPort environment, or testing and
      developing a functioning <TextLink to="https://github.com/decentralized-identity/universal-resolver">
      Universal DID Resolver</TextLink> needs to be as simple as
      <Code>npm install sdk; npm run setup</Code>.  By deploying such test
      systems ourselves, and executing scenarios using the test
      cases described above, we believe we can lower the barrier to
      truly wide and uniquitous adoption of the key technologies
      of decentralized identity.
    </span>
  )
}

const Component = props => {
    const { siteTitle } = getSiteData;
    const setWidth = css`
        width: ${props.width}px;
    `;
    return (
      <Container className="container">
        <h3 className="h4">Wide Adoption</h3>
      </Container>
);
};

export default {
    anchor : '#operations',
    headline: 'Wide Adoption',
    body: <TopText />,
    component:<Component />
};



