import { css } from 'react-emotion';
import { position, size } from 'polished';
import { fontSize, palette } from 'css/variables';

export const gradientBg = css `
    background: rgb(0,255,0);
    background: url('img/topographics.svg'), radial-gradient(circle at center -100%, rgba(0,255,0,1) 0%, rgba(22,24,23,0) 77%);
    background-repeat: repeat;
`;

// Positions

export const positions = {
    absCenter: () => css`
        ${position('absolute', '0')};
        margin: auto;
    `,

    fixedCenter: () => css`
        ${position('fixed', '0')};
    `
};

export const displays = {
    table: () => css`
        ${size('100%')};
        display: table;
        position: relative;
    `,

    vmiddle: () => css`
        ${size('100%')};
        display: table-cell;
        vertical-align: middle;
    `,

    inBlock: () => css`
        display: inline-block;
    `
};

// Text - Sizes, Colors and Alignment.

export const text = {
    small: () => css`
        font-size: ${fontSize.small};
    `,
    medium: () => css`
        font-size: ${fontSize.medium};
    `,
    mediumLg: () => css`
        font-size: ${fontSize.mediumLg};
    `,
    large: () => css`
        font-size: ${fontSize.large};
    `,
    xLarge: () => css`
        font-size: ${fontSize.xLarge};
    `,
    huge: () => css`
        font-size: ${fontSize.huge};
    `,

    center: () => css`
        text-align: center;
    `,
    left: () => css`
        text-align: left;
    `,
    right: () => css`
        text-align: right;
    `,
    justify: () => css`
        text-align: justify;
    `,

    greenNeon: () => css`
        color: ${palette.greenNeon}
    `,

    green: () => css`
        color: ${palette.green}
    `,
    greenDark: () => css`
        color: ${palette.greenDark}
    `,
    greenDarkest: () => css`
        color: ${palette.greenDarkest}
    `,
    greenDarkestAlt: () => css`
        color: ${palette.greenDarkestAlt}
    `,
    grayLight: () => css`
        color: ${palette.grayLight}
    `,
    grayLighter: () => css`
        color: ${palette.grayLighter}
    `,
    grayLightest: () => css`
        color: ${palette.grayLightest}
    `,

    upperCase: () => css`
        text-transform: uppercase;
    `,
};

export const list = {
    none: () => css`
        list-style: none;
    `,
    square: () => css`
        list-style: square;
    `,
    bullet: () => css`
        list-style: bullet;
    `,
}



