import React from 'react';
import Contour from 'components/Contour';
import Logo from 'components/Logo';
import { css } from 'react-emotion';
import { palette } from 'css/variables';
import { HeaderMessage, ListItemLink, ListItemBody,TrailerMessage,
  Section, Container, Text, List, ListItem } from 'css/basicElements';
import { Link } from 'react-static';
import { displays, text } from 'css/utilities';
import TextLink from 'components/TextLink';

const greenNeon = css`
    color: ${palette.greenNeon};
    display: inline;
`;

const white = css`
    color: ${palette.white};
    display: inline;
`;

const columnize = css`
    column-count: 2;
    column-width: 320px;
    column-span: all;
    column-gap: 45px;
    column-fill: balance;
`;


import BridgeDefn from './projects/Bridge';
import iFreedomDefn from './projects/iFreedom';
import POIAndRegistryDefn from './projects/POIAndRegistry';
import ReclusivityDefn from './projects/Reclusivity';
import ABitOfFun from './projects/ABitOfFun';

import CommunityDefn from './projects/visualization/Community';
import OperationsDefn from './projects/visualization/Operations';
import AccessibilityDefn from './projects/visualization/Accessibility';
import LegislativeDefn from './projects/visualization/Legislative';

const visualizationContent = [
  CommunityDefn,
  AccessibilityDefn,
  OperationsDefn,
  LegislativeDefn
];

import UseCaseDefn from './projects/pragmatics/UseCase';
import WideAdoptionDefn from './projects/pragmatics/WideAdoption';

const technicalPragmaticsContent = [
  UseCaseDefn,
  WideAdoptionDefn,
];
const otherContent = [
  BridgeDefn,
  ReclusivityDefn,
  iFreedomDefn,
  //POIAndRegistryDefn,
  ABitOfFun
];

export default () => (
    <React.Fragment>
        <Contour />
        <Section>
        <Container className="container">
        <Logo
            fill={palette.green}
            width={160}
          />
          <HeaderMessage>
            Visualization, Pragmatics, Levity
          </HeaderMessage>
          <h5>
            <ul>
              <li>Korsimoro analytics encourage assessment of the community,
                operational, and legal dimensions of the global
                identity ecosystem.</li>
              <li>
                Korismoro provides technical QA services and 3rd party
                monitoring to improve the
                health of the digital identity community and to promote
                widespread adoption.</li>
              <li>Korsimoro engages in a bit of research and promotes the
                &nbsp;<i>levity of identity.</i>
              </li>
            </ul>
          </h5>
          <hr style={{margin:0, marginBottom:"1em"}}/>
          <h4>Visualization</h4>
          <ListItemBody>
            Understanding the current state of the digital identity ecosystem
            is too complex.  Korismoro brings visibility to this complexity
            through web accessible <TextLink to="https://d3js.org/">D3</TextLink>
            portals and exportable
            <TextLink to="https://gephi.org/">Gephi</TextLink> graphs powered
            by traditional API-backed NoSQL databases.  Using these tools
            we help answer questions about the state of the art in digital
            identity.
          </ListItemBody>
          <List className={columnize} noBullet>
              {visualizationContent.map((item, key) => (
                  <ListItem
                      key={key}
                  >
                      {/*<Link to={"#"+item.anchor}>*/}
                        { item.headline && <ListItemLink>{item.headline}</ListItemLink>}
                      {/*</Link>*/}
                      { item.body && <ListItemBody textMd>{item.body}</ListItemBody> }
                  </ListItem>
              ))}
          </List>

          <h4>Technical Pragmatics</h4>
          <ListItemBody>
            The technology of digital identity remains difficult to access, even
            for the experienced developer.  Discussions and analysis
            in myriad working groups is mired in an endless need to introduce the
            details of Alice and Bob for their immediate purposes.  Both of these
            factors slow the development of a robust global identity ecosystem.
          </ListItemBody>
          <List className={columnize} noBullet>
              {technicalPragmaticsContent.map((item, key) => (
                  <ListItem
                      key={key}
                  >
                      {/*<Link to={"#"+item.anchor}>*/}
                        { item.headline && <ListItemLink>{item.headline}</ListItemLink>}
                      {/*</Link>*/}
                      { item.body && <ListItemBody textMd>{item.body}</ListItemBody> }
                  </ListItem>
              ))}
          </List>


          <h4>Research and Miscellany</h4>
          <List className={columnize} noBullet>
              {otherContent.map((item, key) => (
                  <ListItem
                      key={key}
                  >
                      {/*<Link to={"#"+item.anchor}>*/}
                        { item.headline && <ListItemLink>{item.headline}</ListItemLink>}
                      {/*</Link>*/}
                      { item.body && <ListItemBody textMd>{item.body}</ListItemBody> }
                  </ListItem>
              ))}
          </List>

          <TrailerMessage>Doors open 1/1/2019</TrailerMessage>
          {/*
          <hr style={{margin:0, marginBottom:"1em"}}/>
          {content.map((item, key) => (
              <div id={item.anchor}
                  key={key}
              >
                {item.component}
              </div>
          ))}
          */}
        </Container>
        </Section>
    </React.Fragment>
);



