import React from 'react';
import styled, { css } from 'react-emotion';
import { hideText, position } from 'polished';
import { getSiteData } from 'react-static';
import { Section, Container, Text, List, ListItem } from 'css/basicElements';


const TopText = props => {
  return (
    <span>
      Reculsivity is a fundamental human right, but is bounded by the
      social contract.  Is it possible to regain reclusivity
      while supporting the demands of a global society?  While GDPR is a
      first and enlightened step in the right direction, it is not yet
      a global solution.  In fact, it is actively and persistently
      challenged in autocratic environments
      and in societies hampered by intrasocial wars,
      such as the United State's wars on terror and drugs.
      In terms of specific
      institutions and legal frameworks, is it even practical to regain
      lost reclusivity and if so, how?
    </span>
  )
}

const Component = props => {
    const { siteTitle } = getSiteData;
    const setWidth = css`
        width: ${props.width}px;
    `;
    return (
      <Container className="container">
        <h3 className="h4">Reclusivity Rights</h3>
      </Container>
);
};

export default {
    anchor : '#operations',
    headline: 'Reclusivity Rights',
    body: <TopText />,
    component:<Component />
};



