import React from 'react';
import styled, { css } from 'react-emotion';
import { hideText, position } from 'polished';
import { getSiteData } from 'react-static';
import { Section, Container, Text, List, ListItem } from 'css/basicElements';
import TextLink from 'components/TextLink';

const TopText = props => {
  return (
    <span>
    Your identity is a matter of physics and it has no direct relationship
    to a handheld computer or other device.  Over-reliance upon smartphones,
    software, and hardware gadgetry such as cryptographic processors and
    embedded biometric sensors, is a serious and blossomming threat to the
    integrity of personal identity.  The physical management of a centrally
    manufactured
    and heavily monitored, fundamentally insecure, bricks of plastic and metal
    should never be the foundation upon which a digital identity is anchored.
    Device reliance has endangered the personal
    identity of billions through inappropriate cultivation of
    the myth of device integrity.  Many groups and organizations are
    actively engaged in responding to the threats imposed by governments which
    continually pressure device manufacturers to weaken privacy protection.
    The scope and details
    of this contest can be hard to grasp and will benefit from improved
    visualization of the key players and contests.
    </span>
  )
}

const Component = props => {
    const { siteTitle } = getSiteData;
    const setWidth = css`
        width: ${props.width}px;
    `;
    return (
      <Container className="container">
        <h3 className="h4">Device Decoupling</h3>
      </Container>
);
};

export default {
    anchor : '#operations',
    headline: 'iFreedom - Device Decoupling',
    body: <TopText />,
    component:<Component />
};



